.object {
	display: inline-block;
	font-size: 14px;
	color: #fff;
	margin-left: 10px;
	line-height: 21px;
	padding: 0 7px;
	vertical-align: middle;
	letter-spacing: -.5px;
	background-color: #f73969;
}

.paging_wrap {
	margin: 0 auto;
	text-align: center;
	margin-top: 40px;
	margin-bottom: 70px;
}

.paging_wrap .pageview {
	display: inline-block;
}

.paging_wrap .pageview a {
	display: block;
	float: left;
	color: #333;
	text-align: center;
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
	margin: 0 6px;
	color: #8c8c8c;
	font-weight: bold;
	line-height: 30px;
}

.paging_wrap .pageview a.arrow {
	border: 1px solid #bdbfc2;
	width: 30px;
	height: 30px;
	line-height: 30px;
	margin: 0 3px;
	-webkit-background-size: auto auto;
	background-size: auto;
	background-repeat: no-repeat;
	background-position: center;
}

.paging_wrap .pageview a.prev {
	margin-right: 20px;
	background-image: url("../images/common/prev.png");
}

.paging_wrap .pageview a.prev_dub {
	background-image: url("../images/common/prev_dub.png");
}

.paging_wrap .pageview a.next {
	margin-left: 20px;
	background-image: url("../images/common/next.png");
}

.paging_wrap .pageview a.next_dub {
	background-image: url("../images/common/next_dub.png");
}

.paging_wrap .pageview a.active {
	position: relative;
	color: #6569bc;
}



.bo_search_wrap {
	border-bottom: 1px solid #dcdcdc;
	padding: 20px 20px 50px;
	box-sizing: border-box;
	text-align: center;

	.bo_search_box {
		position: relative;
	}

	.search_text {
		position: relative;
		display: inline-block;
		max-width: 700px;
		width: 95%;
	}

	input[type="text"] {
		margin: 0 auto;
		width: 100%;
		height: 60px;
		border: none;
		border-bottom: 3px solid #000;
		font-size: 1.466rem;
		padding: 0 50px 0 5px;
		box-sizing: border-box;
	}

	input[type="text"]::-webkit-input-placeholder {
		color: #888;
		font-size: 1.466rem;
	}

	input[type="text"]:-ms-input-placeholder {
		color: #888;
		font-size: 1.466rem;
	}

	input[type="text"]::placeholder {
		color: #888;
		font-size: 1.466rem;
	}

	.bo_search_btn {
		width: 45px;
		height: 45px;
		border-radius: 45px;
		background-color: #5e8acc;
		position: absolute;
		right: 5px;
		bottom: 10px;

		i {
			color: #fff;
			font-size: 1.533rem;
		}

		&.btn_print {
			position: relative;
			right: inherit;
			bottom: inherit;
			display: inline-block;
			vertical-align: top;
			top: 4px;
			vertical-align: top;
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center;
			background-image: url("../images/search/ico_print.png");
		}
	}

	.select_search {
		select {
			margin: 0 auto;
			height: 60px;
			border: none;
			border-bottom: 3px solid #000;
			font-size: 1.466rem;
			padding: 0 5px 0 5px;
			box-sizing: border-box;
			margin-right: 10px;
			max-width: 100%;
		}

		.search_text {
			max-width: inherit;
		}

		.search_btn {
			position: relative;
			display: inline-block;
			padding-top: 10px;
			box-sizing: border-box;

			button {
			    right: inherit;
			    bottom: inherit;
			}
		}

		.bo_search_btn {
			position: relative;

			&.btn_print {
				top: inherit;
				margin-left: 5px;
			}
		}

		input[type="text"] {
			width: 45%;
			padding: 0 10px 0 5px;
		}
	}

}

.search_etc_btn {
	position: absolute;
	top: 20px;
	left: 83%;

	button {
		margin-right: 7px;
		height: 36px;
		line-height: 30px;
		border-radius: 36px;
		border: 2px solid #5e8acc;
		color: #5e8acc;
		font-size: 1.067rem;
		padding: 0 20px 0 35px;
		box-sizing: border-box;
		background-size: auto;
		background-repeat: no-repeat;
		background-position: 15px 7px;
		background-image: url("../images/search/btn_location.png");

		&:hover,
		&.active {
			color: #fff;
			background-color: #5e8acc;
			background-image: url("../images/search/btn_location_on.png");

			&.filter_show {
				color: #fff;
				background-color: #5e8acc;
				background-image: url("../images/search/btn_filter_on.png");
			}
		}
	}

	.filter_show {
		background-image: url("../images/search/btn_filter.png");
	}
}

