.bo_search_wrap.search_prev {
	.bo_search_btn {
		width: 100px;
		height: 38px;
		background: #474851;
		border: 1px solid #474851;
		color: #fff;
		font-size: 1.067rem;
		position: relative;
		border-radius: 0;
		bottom: inherit;
	}

	.bo_search_btn i {
		vertical-align: middle;
