	}

	.search_etc_btn {
		display: inline-block;
	}

	.search_etc_btn button {
		width: 100px;
		height: 38px;
		background: #ee3868;
		color: #fff;
		margin-left: 5px;
	}

	.search_etc_btn button img {
		vertical-align: middle;
	}

	.keyword_box {
		text-align: left;
		display: table;
		margin-top: 8px;
		width: auto;
		table-layout: fixed;
	}

	.keyword_box > dt, .keyword_box > dd {
		display: table-cell;
		vertical-align: top;
	}

	.keyword_box > dt {
		position: relative;
		padding-right: 10px;
		margin-right: 10px;
	}

	.keyword_box > dt:after {
		content: '';
		width: 1px;
		height: 80%;
		background: #ccc;
		position: absolute;
		right: 0;
		top: 10%;
	}

	.keyword_box > dt i {
		color: #6569bc;
	}

	.keyword_box > dt p {
		color: #3e3e3e;
		display: inline-block;
	}

