		color: #888;
		display: inline-block;
		margin-left: 10px;
	}

	input[type="text"]::-webkit-input-placeholder {
		color: #888;
		font-size: 1rem;
	}

	input[type="text"]:-ms-input-placeholder {
		color: #888;
		font-size: 1rem;
	}

	input[type="text"]::placeholder {
		color: #888;
		font-size: 1rem;
	}

	img.ui-datepicker-trigger + .bo_search_btn {
		margin-left: 10px;
		height: 37px;
	}
}


@media (max-width: 1280px) {
	.bo_search_wrap {
		padding: 0 30px 40px;

		.search_text {
			float: inherit;
		}
		.search_etc_btn {
			position: relative;
			float: inherit;
    		left: inherit;
		}
		.bo_search_box {
			overflow: inherit;
		}

		&.search_prev {
			padding: 20px 0;

			.bo_search_box {
				width: 95%;
				margin: 0 auto;

				input[type="text"] {
					width: 34%;
				}
			}

			form {
				display: block;
			}

			img.ui-datepicker-trigger + .bo_search_btn {
				width: 18%;
			}
		}
	}

	.search_etc_btn {
		button {
			font-size: 1rem;
		}
	}

}

@media (max-width: 1000px) {

	.bo_search_wrap {
		.bo_search_btn {
			&.btn_print {
				position: absolute;
			}
		}
	}
}




