	margin: 2px 0 0 -8px;
}

.sorting_wrap .checkbox_type li {
	float: left;
	margin: 8px 0 0 8px;
}

.sorting_wrap .checkbox_type li label {
	display: block;
	height: 38px;
	line-height: 38px;
	position: relative;
	cursor: pointer;
	border: 1px solid #dbdbdb;
	padding: 0 20px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	border-radius: 38px;
}

.sorting_wrap .checkbox_type li input[type="checkbox"],
.sorting_wrap .checkbox_type li input[type="radio"] {
	width: 0;
	height: 0;
	position: absolute;
}

.sorting_wrap .checkbox_type li input[type="checkbox"]:checked + label,
.sorting_wrap .checkbox_type li input[type="radio"]:checked + label {
	background-color: #5e8acc;
	border: 1px solid #5e8acc;
	color: #fff;
	background-size: auto;
	background-repeat: no-repeat;
	background-position: right 15px center;
}

.sorting_wrap {
	.checkbox_bx {
		margin-top: 20px;

		label {
