.checkbox_bx {
	margin-top: 12px;
	margin-bottom: 18px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

.checkbox_bx label {
	display: inline-block;
	position: relative;
	vertical-align: middle;
	cursor: pointer;
	height: 18px;
}

.checkbox_bx label:before {
	content: '';
	background: #fff;
	border: 2px solid #6569bc;
	display: inline-block;
	vertical-align: middle;
	width: 14px;
	height: 14px;
	text-align: center;
	border-radius: 3px;
}

.checkbox_bx span {
	padding-left: 5px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

.checkbox_bx input[type="checkbox"] {
	display: block;
	width: 0;
	height: 0;
	opacity: 0
}

.checkbox_bx input[type="checkbox"]:checked + label:after {
	content: "";
	padding: 3px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	text-align: center;
	position: absolute;
	border: 1px solid #6569bc;
	border-width: 0 3px 3px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	transform: rotate(45deg);
	top: 2px;
	left: 3px;
	width: 8px;
	height: 10px;
	border-radius: 3px;
}

