.file_wrap {
	.file_box {
		overflow: hidden;
	}

	input[type="text"] {
		width: 90%;
		height: 36px;
		border: 1px solid #ccc;
		appearance: none;
		float: left;
		background: #fff;
		padding-left: 1rem;
		box-sizing: border-box;
	}

	label {
		border: 1px solid #9d9d9d;
		color: #6b6b6b;
		font-size: inherit;
		line-height: normal;
		vertical-align: middle;
		background: #fff;
		cursor: pointer;
		height: 35px;
		line-height: 35px;
		width: 9%;
		text-align: center;
		float: right;
	}

	input[type="file"] {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}

	ul {
		margin-top: 7px;

		li {
			border: 1px solid #ccc;
			border-top: none;
			padding: 5px 0;
			overflow: hidden;
			
			&:first-child {
				border-top: 1px solid #ccc;
			}

			a {
				display: inline-block;
				padding-left: 3rem;
				-webkit-box-sizing: border-box;
				-moz-box-sizing: border-box;
				-ms-box-sizing: border-box;
				box-sizing: border-box;
				background-image: url("../images/board/file_bg.png");
				-webkit-background-size: auto auto;
				background-size: auto;
				background-repeat: no-repeat;
				background-position: 1rem center;
			}

			button {
				border: 1px solid #ccc;
				color: #333;
				padding: 5px;
				box-sizing: border-box;
				display: inline-block;
				margin-left: 10px;
			}

			p {
				float: left;
				width: 90%;

				&.roll_btn {
					float: right;
					max-width: 70px;
					width: 10%;
					text-align: right;
					padding-right: 10px;
					box-sizing: border-box;
					
					button {
						margin-left: 0;
					}
				}
			}
		}
	}
}

.file_del_box {
	margin-bottom: 10px;

	.file_del_cnt {
		border: 1px solid #ccc;
		padding: 8px 5px;
		border-top: none;

		p + .file_del_cnt {
			padding: 8px 5px;
			border: 1px solid #ccc;
			border-top: 2px solid #2f8ab9;
		}
	}

	p {
		margin: 5px 0px;
		box-sizing: border-box;
		position: relative;
		padding-left: 10px;

		&:after {
			content: '·';
			position: absolute;
			top: 0;
			left: 0;
		}

		+ .file_del_cnt {
			padding: 8px 5px;
			border: 1px solid #ccc;
			border-top: 2px solid #a82260;
		}
	}
}




.board_search_wrap {
	display: table;
	table-layout: fixed;
	width: 100%;
	border-spacing:0px;
	border:1px solid #ccc;
	background: #f7f7f7;

	select, input[type="text"] {
		padding: 5px 20px;
		box-sizing: border-box;
		width: 100%;
		height: 68px;
		background-color: #f7f7f7;
		border: none;
		-webkit-border-radius: 0;
		position: relative;
		font-size: 1.42857rem;
	}

	.board_search_box {
		display: table-cell;
		vertical-align: middle;
		padding: 0;
		border-left: 1px solid #ccc;
		position: relative;

		&:first-child,
		+ .board_search_box {
			width: 300px;
		}

		&:first-child {
			border-left: none;
		}
	}

	.board_text_box {
		border-left: 1px solid #ccc;
		position: relative;

		&:first-child {
			border-left: none;
		}

		input[type="text"] {
			line-height: 68px;
			padding-right: 70px;
		}
		input[type="text"]::-webkit-input-placeholder { font-size: 1.42857rem }
		input[type="text"]:-ms-input-placeholder { font-size: 1.42857rem }
		input[type="text"]::placeholder { font-size: 1.42857rem }

		.btn_search {
			position: absolute;
			top: 0;
			right: 0;
			width: 70px;
			height: 70px;
			background-image: url(../images/sub/ico_search_btn.png);
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: 32px auto;
		}
	}

	&.bsbox_3 {
		.board_search_box {
			width: 200px;
		}
	}

	&.bsbox_4 {
		.board_search_box {
			width: 320px;

			&:first-child {
				border-left: none;
			}
		}
		.board_text_box {
			border-left: none;
			border-top: 1px solid #ccc;
		}
	}

	&.bsbox_wide {
		position: relative;
		z-index: 50;

		.board_search_box {
			&:first-child {
				width: 45%;
				position: relative;

				button {
					padding: 5px 20px;
					-webkit-box-sizing: border-box;
					-moz-box-sizing: border-box;
					box-sizing: border-box;
					width: 100%;
					height: 68px;
					background-color: #f7f7f7;
					border: none;
					-webkit-border-radius: 0;
					position: relative;
					font-size: 1.42857rem;
					text-align: left;

					+ i {
						position: absolute;
						right: 10px;
						top: 25px;
						font-size: 1.5rem;
					}

					&.active {
						background-color: #46495c;
						color: #fff;

						+ i {
							color: #fff;
						}
					}
				}
			}
		}
	}
}



