		text-align: center;
		float: left;
		margin: 0 20px;
		position: relative;

		&:after {
			content: '';
			width: 20px;
			border-top: 1px dashed #686868;
			position: absolute;
			right: -30px;
			top: 16px;
		}


		&.visited {
			border: 2px solid #5e8acc;
			background-color: #5e8acc;
			color: #fff;
			font-family: 'NanumBarunGothic-B';

			i {
				line-height: 36px;
			}
		}

		&.active {
			width: auto;
			border: 2px solid #5e8acc;
			background-color: #fff;
			color: #5e8acc;
			font-family: 'NanumBarunGothic-B';
			font-size: 0.933rem;
			padding: 0 20px;
		}
	}

	.jgm_step_box {
		text-align: center;
		display: inline-block;

		> div {
			float: left;

			&:last-child {
				p {
					&:last-child {
						margin-right: 0;

						&:after {
							display: none;
						}
					}
				}
			}
		}
	}
}





@media (max-width: 680px) {


	.jgm_step {
		padding: 10px 0 30px;

		p {
			width: 30px;
			height: 30px;
			border-radius: 30px;
			line-height: 30px;
			display: inline-block;
			float: inherit;

			&.visited {
				i {
					line-height: 30px;
				}
			}

			&:after {
				right: -32px;
			}
		}

		.jgm_step_box {
			> div {
				float: none;
				display: block;
				margin-top: 20px;
				overflow: hidden;
				text-align: center;
			}
		}
	}

}

@media (max-width: 480px) {


	.jgm_step {
		padding: 20px 0;

		p {
			margin: 0 10px;
			width: 25px;
			height: 25px;
			border-radius: 25px;
			line-height: 25px;

			&.visited {
				i {
					line-height: 25px;
				}
			}

			&.active {
				padding: 0 12px;
			}

			&:after {
				width: 7px;
				right: -16px;
				top: 13px;
			}
		}
	}

}


